<script>
import { getPromos } from "@/api"
import { getProductsByFilters } from "@/api/products"
import { mapActions, mapState } from "vuex"
import { actionTypes } from "@/store/modules/stock"
import { getPromo } from "@/api"

export default {
  name: "PageStocksProducts",
  components: {
